












import { Vue, Component, Prop } from 'vue-property-decorator';
import BaseImage from '@commonComponents/BaseImage.vue';
import ImagePreview from '@commonComponents/ImagePreview';

@Component({
  name: 'YImage',
  components: {
    BaseImage,
  },
})
export default class extends Vue {
  @Prop({ type: String, default: '' })
  private src!: string;

  @Prop({ type: String, default: '' })
  private alt!: string;

  @Prop({ type: String, default: '' })
  private title!: string;

  private onPreviewImage(): void {
    const imagePreview = new ImagePreview(this.src, this.title, this.alt);
    imagePreview.mounted();
  }
}
